.App {
  text-align: center;  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.bubbles{
  position:absolute;
  width:100%;
  height: 100%;
  z-index:0;
  overflow:hidden;
  top:0;
  left:0;
  background: radial-gradient(at top, rgba(0,212,255,.7) 0%, rgba(2, 21, 39, 0.99) 75%, rgb(0, 0, 0) 98%);
}
.bubble{
  position: absolute;
  bottom:-100px;
  width:40px;
  height: 40px;
  background: rgb(2,0,39);
  background: radial-gradient(circle, rgba(2,0,39,.1) 30%, rgba(158,237,255,1) 95%, rgba(0,212,255,1) 100%);
  border-radius:50%;
  opacity:0.5;
  animation: rise infinite ease-in;
  animation-duration: 10s;
  opacity: .2;
}
.bubble:nth-child(1){
  width:30px;
  height:30px;
  left:10%;
  animation-duration:8s;
}
.bubble:nth-child(2){
  width:10px;
  height:10px;
  left:20%;
  animation-duration:5s;
  animation-delay:1s;
}
.bubble:nth-child(3){
  width:40px;
  height:40px;
  left:35%;
  animation-duration:7s;
  animation-delay:2s;
}
.bubble:nth-child(4){
  width:20px;
  height:20px;
  left:50%;
  animation-duration:11s;
  animation-delay:0s;
}
.bubble:nth-child(5){
  width:35px;
  height:35px;
  left:55%;
  animation-duration:6s;
  animation-delay:1s;
}
.bubble:nth-child(6){
  width:45px;
  height:45px;
  left:65%;
  animation-duration:8s;
  animation-delay:3s;
}
.bubble:nth-child(7){
