  height:25px;
  left:70%;
  animation-duration:12s;
  animation-delay:2s;
}
.bubble:nth-child(8){
  width:25px;
  height:25px;
  left:80%;
  animation-duration:6s;
  animation-delay:2s;
}
.bubble:nth-child(9){
  width:15px;
  height:15px;
  left:70%;
  animation-duration:5s;
  animation-delay:1s;
}
.bubble:nth-child(10){
  width:55px;
  height:55px;
  left:25%;
  animation-duration:10s;
  animation-delay:4s;
}
.bubble:nth-child(11){
  width:40px;
  height:40px;
  left:10%;
  animation-duration:14s;
}
.bubble:nth-child(12){
  width:20px;
  height:20px;
  left:20%;
  animation-duration:5s;
  animation-delay:3s;
}
.bubble:nth-child(13){
