}
@keyframes rise{
  0%{
    bottom:-100px;
    transform:translateX(0);
  }
  50%{
    transform:translate(100px);
  }
  100%{
    bottom:1080px;
    transform:translateX(-200px);
  }
}

.logo{
  position:absolute;
  top:50%;
  transform: translateY(-50%);
  text-align: center;
  width: 100%;
}

.logo img{
  margin: 0px auto;
  padding:10px;
  width: 50%;
  max-width: 500px;
  opacity: .7;
}

.logo p{
  color: rgb(198, 201, 233);
  font-size:18px;
  opacity: .6;
}

.logo a{
  display: block;
  border-radius: 10px;
  padding:15px;
  margin:20px auto;
  margin-top: 80px;
  max-width: 120px;
  background-color: rgba(11, 25, 54, 0.4);
  color: white;
  font-weight: 600;
  text-decoration: none;
}
